.login-page{
    background: linear-gradient(to bottom right,#061121 50%,#105BAA 100%);
    flex-direction: column;
    height: 100vh;
    .register-header-letter1{
        font-size: 2rem;
        padding: 0;
        margin: 0;
    }
    .register-header-letter2{
        font-size: 1rem;
        padding: 0;
        margin: 0;
    }
    .login-card{
        width: 500px;
        .login-input{
            min-width: 100%;
        }
        .login-btn{
            width: 100%;
            background: rgb(12, 108, 242);
            color: white;
            padding: 15px;
            &:hover{
                background: rgb(10, 86, 193);
            }
        }
        .create-wallet-icon{
            background: #0c6cf2;
            border-radius: 45px;
            width: 50px;
            height: 50px
        }
    }
}
.auth-footer{
    background-color: white;
    margin-top: 0.2rem;
}